.card {
  position: relative;
  min-height: 623px;
  background-position: 50%;
  background-size: cover;
  background-repeat: no-repeat;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  background-color: #c4c4c4;

 a {
    display: block;
    width: 100%;
    height: 100%;
  }
}

.card--production {
  background-image: url("../img/background-goods-1-desktop@1x.png"), linear-gradient(#1B2D37 100%, #1B2D37 100%);

}

.card--mounting {
  background-image: url("../img/background-goods-2-desktop@1x.png"), linear-gradient(#1B2D37 100%, #1B2D37 100%);
}

.card--components {
  background-image: url("../img/background-goods-3-desktop@1x.png"), linear-gradient(#1B2D37 100%, #1B2D37 100%);
}

.card h3 {
  margin: 0;
  padding-top: 53px;
  padding-left: 44px;
  padding-right: 170px;
  font-size: 20px;
  line-height: 1.5;
  color: #ffffff;
  font-family: "RobotoCondensed-Regular", Arial, sans-serif;
  font-weight: 400;
}

.card__arrow {
  position: absolute;
  bottom: 0;
  right: 0;
  background-color:#00B2FF;
  width: 63px;
  height: 63px;
  background-position: 50%;
    background-repeat: no-repeat;
    background-image: url("../img/arrow.svg");
}

